<template>
          <v-card class="mt-0 ml-1">
                <v-tabs v-model="tab" color="#0080FF" align-tabs="center">
                    <v-tab>Présence</v-tab>
                    <v-tab>Item Two</v-tab>
                    <v-tab>Item Three</v-tab>
                </v-tabs>
                <div class="card" v-if="tab == 0">
                    <div class="card-header">
                    <div class="row d-flex justify-content-start">
                        <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3 margin-bottom ml-2">
                            <label>Agence : </label>
                            <div class="data-picker" style="width:100%">
                                <ui-select
                                    placeholder="Choisir une usine"
                                    :options="agenceListe"
                                    @change="loadConfig()"
                                    v-model="agenceSelect"
                                ></ui-select>
                            </div>
                        </div>
                        <div class="col-xl-4 col-lg-4 col-md-5 col-sm-6 col-xs-6 mb-1">
                                <label>{{periode}} : </label>
                                <div class="data-picker" style="width:100%">
                                    <el-date-picker
                                        v-model="drp"
                                        type="daterange"
                                        unlink-panels
                                        range-separator="-"
                                        format="dd/MM/yyyy"
                                        :clearable="clearable"
                                        @change="loadFilter()"
                                        :picker-options="pickerOptions">
                                    </el-date-picker>
                                </div>
                        </div>
                       
                        
                    </div>
                    </div>
                    <div class="card-body">
                        <div v-if="empty">
                            <el-empty :image-size="200"></el-empty>
                        </div>
                        <div v-else>
                            <p>Body test card item 01 </p>
                        </div>
                        
                    </div>
                </div>
                <div class="card" v-if="tab == 1">
                    <div class="card-header">
                        <h1>Header 02</h1>
                    </div>
                    <div class="card-body">
                        <p>Body test card item 01 </p>
                    </div>
                </div>
                <div class="card" v-if="tab == 2">
                    <div class="card-header">
                        <h1>Header 03</h1>
                    </div>
                    <div class="card-body">
                        <p>Body test card item 01 </p>
                    </div>
                </div>
            </v-card>
</template>
<script>

import axios from 'axios';
import moment from 'moment';
import NProgress from 'nprogress';
import { watch } from 'vue';

  export default {

    data() {
        return {
            tab: null,

            // Filter;
            type: '',
            agenceListe: [],
            agenceSelect:[],
            all_data: [],
            all_date : [],
            empty: false,
            periode: 'Choisir la periode',
            start:  moment().subtract(12, 'month').startOf('month').toDate(),
            end: moment().toDate(),
            clearable: true,
            //drp : [moment().subtract(200, 'isoweek').startOf('isoweek').toDate(), moment().subtract(1, 'isoweek').endOf('isoweek').toDate()],
            drp : [moment().subtract(1, 'day').startOf('day').toDate(), moment().toDate()],
            pickerOptions: {
                shortcuts: [{
                    text: 'Mois precedent',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'month').startOf('month').toDate(), moment().subtract(1, 'month').endOf('month').toDate()]);
                    }
                }, 
                {
                    text: 'Mois en cours',
                    onClick(picker) {
                        picker.$emit('pick', [moment().startOf('month').toDate(), new Date()]);
                    }
                }, 
                {
                    text: 'Semaine precedente',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'week').startOf('isoWeek').toDate(), moment().subtract(1, 'week').endOf('isoWeek').toDate()]);
                    }
                }, 
                {
                    text: 'Semaine en cours',
                    onClick(picker) {
                        picker.$emit('pick', [moment().startOf('isoWeek').toDate(), new Date()]);
                    }
                }, 
                {
                    text:'Avant hier',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(2, 'day').toDate(), moment().subtract(2, 'day').toDate()]);
                    }
                }, 
                {
                    text: 'Hier',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'day').toDate(), moment().subtract(1, 'day').toDate()]);
                    }
                }, {
                    text: 'Aujoudhuit',
                    onClick(picker) {
                        picker.$emit('pick', [moment().toDate(), new Date()]);
                    }
                }],
                format:'dd MM yyyy',
                firstDayOfWeek: 1,
                disabledDate: this.disabledDueDate
            },
        }
    },
    watch: {
        tab : function () {
            console.log(this.tab);
        } 
    },
    created () {
        this.loadFilter();
    },

    methods: {

        async loadFilter()
        {
            NProgress.start();

            try {
                let crm = {
                    start: moment(new Date(this.drp[0])).startOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                    end: moment(new Date(this.drp[1])).endOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                };



                const response = await axios.get('xperf/report/filter');


                var res = response.data.data;
                console.log(res);

                var tab = [];
                var name = [];

                res.forEach(element => {
                    
                    if(element.code != 'DIR' && element.code != 'EXT') {
                        if (!name.includes(element.nom)) {
                            name.push(element.nom);
                            tab.push({
                                label: element.nom,
                                value: element.code
                            });
                        }
                    }
                });

                this.agenceListe = tab;

                if (name.length > 1) {
                    this.agenceListe.unshift({
                        label: 'Tous',
                        value: 'Tous'
                    });
                };

                this.agenceSelect = this.agenceListe[0];

                 this.loadConfig();
                
            } catch (error) {
                this.$notify({
                  type: 'error',
                  title: "Echec de recuperation"
              });
            }

            NProgress.done();

        },

        async loadConfig() {

            // Start loading
            this.loading = true;
            NProgress.start();
            
            let crm = {
                start: moment(new Date(this.drp[0])).startOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                end: moment(new Date(this.drp[1])).endOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                agence: this.agenceSelect['value']
            };

            console.log(crm);
            const response = await axios.post('/xperf/report/log', crm);
            var data = response.data.all_data;
            console.log(data.length);


            if (data.length > 0) {
                this.empty = false;
                
                var date = [];

                data.forEach(element => {
                    if (!date.includes(element.jour)) {
                        date.push(element.jour);  
                    }
                });
                this.all_date = date;
                this.oderByDate(data);

            }
            else {
                this.empty = true;
            }

            
            NProgress.done();
        },

        oderByDate(data) {
            
            var auteur_id = [];

            data.forEach(element => {
                if (!auteur_id.includes(element.auteur)) {
                    auteur_id.push(element.auteur);  
                }
            });
            
            
            for (let index = 0; index < auteur_id.length; index++) {

                const element = auteur_id[index];
                var data_user = [];
                // regroupement par utilisateur

                data.forEach(el =>{
                    if (el.auteur == element) {
                        data_user.push(el);
                    }
                });

                console.log("data_user");
                console.log(data_user);

                    for (let index = 0; index < this.all_date.length; index++) {
                        const el_date = this.all_date[index];

                        data_user.forEach(el_user => {

                            if (el_user.jour == el_date) {
                                console.log("jours");
                            }
                        });
                        
                    }
                
            }
            


        }
    }

    
  }
</script>